<template>
<div class="container basket-card">
<div class="row">
<div class="col-3"><img class="avatar" :src="require('../assets/' + prop_basket.image)" alt=""></div>
<div class="col-7 border-line">
<div class="basket-card-title">
<div><b>{{prop_basket.title}}</b></div>
<div><small>ед.</small></div>
</div>
<div class="basket-card-price">Итого: {{prop_basket.price * prop_basket.quantity}} Р</div>
</div>
<div class="col-2 border-line">
<div class="q-min-max" @click="onAddInBasket"><i class="fa fa-plus"></i></div>
<div class="quantity">{{prop_basket.quantity}}</div>
<div class="q-min-max" @click="onDelFromBasket"><i class="fa fa-minus"></i></div>
</div>
</div>
</div>
</template>

<script>
export default {
  name: 'BasketCard',
  props:{
  prop_basket: {
  type: Object,
  default: function () {
  return {}
  }
  }
},
methods:{
onAddInBasket(){
this.$store.dispatch("addInBasket", this.prop_basket);
},
onDelFromBasket(){
this.$store.dispatch("delFromBasket", this.prop_basket);
}
},
}
</script>


<style scoped>
.basket-card{
background-color:#fff;
margin-top:-1px;
}
.border-line{
border-top: 1px solid #F2F2F2;
border-bottom: 1px solid #F2F2F2;
}
.q-min-max{
margin-top:10px;
margin-bottom:10px;
width:30px;
height:30px;
border-radius:15px;
background-color:#e59e1f;
color:#fff;
cursor:pointer;
line-height:31px;
}
.quantity{
width:30px;
color:#e59e1f;
}
.avatar{
margin-top:15px;
width:60px;
height:60px;
border-radius:30px;
}
.basket-card-title{
position:absolute;
left:10px;
top:10px;
text-align:left;
}
.basket-card-price{
position:absolute;
left:10px;
bottom:10px;
color:#e59e1f;
font-size:14px;
}
</style>
