<template>
<div class="pro-basket">

<BarBasket class="fixed-top"/>

<div v-if="basket.length > 0">
<BasketCard v-for="product in basket" :key="product.title" :prop_basket="product"/>

<div class="container">
<div class="row mt-3">
<div class="col-6 basket-footer-left">Самовывоз:</div>
<div class="col-6 basket-footer-right">
<b-form-checkbox switch></b-form-checkbox>
</div>
</div>
</div>

<div class="container">
<div class="row mt-3">
<div class="col-6 basket-footer-left">Сумма заказа:</div>
<div class="col-6 basket-footer-right">{{sumBasket}} Р</div>
</div>
</div>

<div class="container">
<div class="row mt-3">
<div class="col-6 basket-footer-left">Доставка:</div>
<div class="col-6 basket-footer-right">Бесплатно</div>
</div>
</div>

<div class="container">
<div class="row mt-3">
<div class="col-6 basket-footer-left mb-3"><b>К оплате:</b></div>
<div class="col-6 basket-footer-right"><b>{{sumBasket}} Р</b></div>
</div>
</div>

<div class="basket-button-footer fixed-bottom mb-3">ОФОРМИТЬ ЗАКАЗ</div>
</div>

<div v-else class="basket-empty">
<div class="basket-shopping">
<i class="fa fa-shopping-basket fa-5x"></i>
<div class="add-in-basket">
<i class="fa fa-plus"></i>
</div>
</div>
</div>

</div>
</template>

<script>
import BarBasket from '../components/BarBasket.vue'
import BasketCard from '../components/BasketCard.vue'
export default {
  name: 'Basket',
  components:{
  BarBasket,
  BasketCard,
  },
  computed:{
  basket(){
  return this.$store.getters.basket;
  },
  sumBasket(){
  var basketB = this.$store.getters.basket;
  var sum = 0;
  for(var s = 0; s < basketB.length; s++){
  sum = sum + basketB[s].quantity * basketB[s].price;
  }
  return sum;
  }
  },
}
</script>


<style scoped>
.pro-basket{
margin-bottom: 150px;
max-width: 600px;
margin-left: auto;
margin-right: auto;
background-color:#F8F8F8;
}
.basket-footer-left{
text-align:left;
font-size:15px;
color:#000;
}
.basket-footer-right{
text-align:right;
font-size:15px;
color:#000;
}
.basket-button-footer{
/*
position:fixed;
bottom:20px;
left:0;
right:0;
*/
width:290px;
height:50px;
background-color:#e59e1f;
color:#fff;
font-weight:600;
font-size:12px;
border-radius:25px;
margin-left:auto;
margin-right:auto;
text-align: center;
line-height:50px;
cursor:pointer;
}
.basket-empty{

}
.basket-shopping{
width:100px;
height:100px;
position:absolute;
top:0px;
bottom:0px;
left:0px;
right:0px;
margin:auto;
}
.add-in-basket{
position:absolute;
bottom:0px;
right:0px;
width:40px;
height:40px;
border-radius:20px;
background-color:#e59e1f;
color:#fff;
cursor:pointer;
line-height:40px;
border:2px solid #fff;
}
.fa-shopping-basket{
color:#e59e1f;
text-shadow: 4px 4px #ebebeb;
}
</style>
